body{
    background-color: rgb(54, 54, 54);
    margin: 0px 0px;
    padding: 0px 0px;
}
.mainApp{
    margin:20px auto;
    width:317px;
    height: auto;
    background-color: rgb(22,27,33);
    color: white;
}
.Top-one .img{
    height: 317px;
    width: 317px;
    overflow: hidden;
}
.Main{
    height: auto;
    width: 317px;
    margin: 0px 0px 20px 0px;
    padding: 0px 0px;
}

.Main .intro {
    text-align: center;
}
 .Main .intro h3{
    display: block;
    height:25.81px;
    width:178.06px;
    margin: auto auto;
    font-weight: 25px;
    overflow: hidden;
     align-items: center; 
}

.Main .intro p{
    display: block;
    margin: auto auto;
    height:14.19px;
    width: 178.06px;
    font-size: x-small;
    color: rgb(95, 114, 55);
}
.Main .intro a{
    width:228px;
    height: 16px;
    font-size: xx-small;
    text-decoration: none;
} 
.Main .Social{
    display: flex;
    justify-content: center;
    gap: 50px;
    
}
.Main .email,.Lnkd{
    width: 105px;
    height: 28px;
    border-radius: 6px;
    margin-top: 10px;
    text-align: center;
    align-items: center;
}
.Main .Lnkd{
    background-color: rgb(80,147,226);
}
.Main .info article{
    height:auto;
    width:260px;
    margin:auto auto;
    margin-bottom: 0px;
    font-weight: lighter;
    font-size: x-small;
    overflow:hidden;
}
.Main .info article h2,h3{
    font-weight: bold;
    font-size: large;
    margin-bottom: 0px;
}
.footer-icons{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 317px;
    height: 66.0px;
    background-color: rgb(22,22,25);
}

.footer-icons .social-icon{
    color: rgb(204, 201, 201);
    width: 26.32px;
    height: 26.32px;

}
button:hover,.social-icon:hover{
    cursor: pointer;
    transform: scale(1.1); /* smooth scale on hover */
    transition: all 0.3s ease; /* smooth transition */
}